.container-cert {
    position: relative;
    overflow: hidden;
}

.content-cert {
    position: relative;
    width: 100%;
}

.image-container-cert {
    position: relative;
    width: 100%;
    height: 500px;
    /* Altura deseada para la imagen */
}

.image-container-cert img {
    width: 100%;
    height: 100%;
    object-fit: cover;

}

.image-overlay-cert {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}

.centered-text-cert {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #fff;
    width: 80%;
    z-index: 1;
    /* Asegura que el texto esté por encima de la capa oscura */
}

.cert-logo img {
    width: 140px;
    /* Ancho deseado del logo */
    height: auto;
    /* Altura automática para mantener la proporción */
    filter: invert(1);
    /* Invierte los colores de la imagen (cambia negro a blanco) */

}


.titulo-servicios-cert {
    font-size: 36px;
    /* Tamaño del título */
    margin-bottom: 16px;
}

h5 {
    font-size: 20px;
    /* Tamaño del subtítulo */
}

/* Estilos para el contenedor adicional */
.addcert-container {
    display: flex;
    overflow: hidden;
    background-image: linear-gradient(to bottom, #ffffff, #d8deff);

}


/* Estilos para el contenido del contenedor */
.addcert-content {
    margin-top: 5%;
    margin-bottom: 5%;
    display: flex;
    width: 100%;
    margin-left: 10%;
}

/* Estilos para el texto y la lista */
.addcert-text {
    flex: 1;
    padding: 30px;
    text-align: justify;

}

.addcert-text h4 {
    font-size: 24px;
    font-weight: bold;
    color: #000000;
    margin-bottom: 20px;
    text-align: justify;

}

.addcert-text p,
.addcert-text li {
    font-size: 16px;
    line-height: 1.6;
    color: #000000;
    margin-bottom: 10px;
}

.addcert-text ul {
    padding-left: 20px;
}

/* Estilos para la imagen */
.addcert-image {
    flex: 1;
    position: relative;
    overflow: hidden;
    max-width: 50%;
    margin-left: 10%;
    margin-right: 10%;
    margin-top: 9%;



}

.image-wrapper-cert img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
    border-radius: 10px;
}

.image-wrapper-cert:hover img {
    transform: scale(1.1);
}

/* Media query para ajustar diseño en pantallas más pequeñas */
@media (max-width: 768px) {
    .addcert-container {
        flex-direction: column;
    }

    .addcert-text,
    .addcert-image {
        width: 100%;
    }
}