.contenedordeproductos {
  margin-top: 10%;
  margin-left: 5%;
  padding-right: 5%;
  margin-bottom: 5%;


}

.product-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  /* Hasta 4 elementos por fila */
  gap: 20px;
  /* Espacio entre tarjetas */

}

.product-card {
  background-color: #e2e2e2;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.product-info {
  margin-top: auto;

}

.product-card img {
  width: 100%;
  /* Ajusta el ancho de la imagen al 100% del contenedor */
  height: 200px;
  /* Establece una altura fija para las imágenes */
  object-fit: cover;
  /* Ajusta la imagen para que cubra el contenedor manteniendo la proporción */
  border-radius: 8px;
  /* Agrega bordes redondeados a las imágenes */
}


/* Estilos CSS para centrar la paginación */
.pagination-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 5%;

}

.menu-container {
  background-color: #000000;
  /* Cambia el color de fondo del contenedor del menú */
  padding: 1px;

  /* Añade un espacio interno alrededor del menú */
  margin-bottom: 2%;
}

.menu-container .ant-menu {
  border-bottom: none;
  /* Elimina el borde inferior del menú horizontal */
}

.contactanosImg {
  position: relative;
  /* Establece la posición como relativa para que los pseudo-elementos se posicionen correctamente */
  margin-bottom: 5%;
}

.contactanosImg::before {
  content: "";
  /* Añade contenido al pseudo-elemento */
  position: absolute;
  /* Posición absoluta para cubrir completamente el contenedor */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  /* Color negro con opacidad del 50% */
}

.contactanosImg img {
  width: 100%;
  height: 600px;
  object-fit: cover;
}