.blog-container {
  padding: 20px;
}

.news-container {
  margin-top: 20px;
  margin-left: 0%;
  margin-right: 0%;

}

.card-row {
  display: flex;
  flex-wrap: wrap;
  /* Permitir el ajuste automático de las tarjetas en múltiples filas */
  justify-content: flex-start;
  /* Alinear las tarjetas desde el lado izquierdo */
  gap: 100px;
  /* Espacio entre las tarjetas */
  margin-left: 150px;
  margin-right: 0px;
  margin-top: 5%;


}

.card-blog {
  border: 1px solid #939393;
  border-radius: 5px;
  padding: 20px;
  width: calc(33.33% - 20px);
  /* Ancho máximo para 3 columnas por fila (con espacio entre tarjetas) */
  max-width: 300px;
  /* Espacio debajo de cada tarjeta */
}

.card-blog h3 {
  margin-bottom: 10px;
}

.card-blog img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 5px;
}

.pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.page-item {
  margin-right: 5px;
}

.page-link {
  cursor: pointer;
  color: #007bff;
  border: 1px solid #dee2e6;
  padding: 5px 10px;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.page-link:hover {
  background-color: #f8f9fa;
}