.Moni-Principal {
    background-color: aliceblue;
}

.container-moni {
    position: relative;
    overflow: hidden;
}

.content-moni {
    position: relative;
    width: 100%;
}

.image-container-moni {
    position: relative;
    width: 100%;
    height: 500px;
    /* Altura deseada para la imagen */
}

.image-container-moni img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.image-overlay-moni {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    /* Color oscuro con opacidad del 50% */
}

.centered-text-moni {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #fff;
    width: 80%;
    z-index: 1;
    /* Asegura que el texto esté por encima de la capa oscura */
}

.logo-moni img {
    width: 140px;
    /* Ancho deseado del logo */
    height: auto;
    /* Altura automática para mantener la proporción */
    filter: invert(1);
}

.container-moni2 {
    max-width: 1300px;
    margin: 0 auto;
    padding: 20px;
}

.content-wrapper-moni2 {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    /* Alinear elementos al inicio verticalmente */
}

.content-wrapper-moni2 img {
    width: 40%;
    height: auto;
    border-radius: 8px;
    margin-right: 20px;
}

.content-wrapper-moni2 h5 {
    flex: 1;
    font-size: 20px;
    line-height: 1.4;
    /* Ajustar el line-height para alinear mejor las líneas */
    text-align: justify;
    /* Justificar el texto para alinear las líneas al final */
    margin-left: 20px;
    /* Ajustar el margen izquierdo según sea necesario */
}

.moniimagescontenedor {
    display: flex;
    flex-direction: column;
    /* Cambia de fila a columna */
    justify-content: center;
    align-items: center;
    /* Alinea elementos al centro horizontalmente */
}

.moniimages {
    width: 100%;
    margin-bottom: 20px;
    /* Añade un margen inferior entre las imágenes */
    padding: 10px;
    box-sizing: border-box;
    height: auto;
    display: flex;
    justify-content: center;
}

.moniimages img {

    margin-top: 5%;
    max-width: 100%;
    /* Ajusta el ancho máximo de la imagen */
    max-height: 400px;
    /* Establece la altura máxima deseada para la imagen */
    object-fit: cover;
    /* Ajusta la imagen para cubrir el contenedor */
    transition: transform 0.3s ease;
    border-radius: 10px;


}

.moniimages:hover img {
    transform: scale(1.1);
}


@media (max-width: 768px) {
    .content-wrapper-moni2 {
        flex-direction: column;
        /* Cambiar a una disposición de columna en pantallas más pequeñas */
    }

    .content-wrapper-moni2 img {
        width: 100%;
        /* Ajustar el ancho de la imagen al 100% en pantallas más pequeñas */
        margin-bottom: 20px;
        /* Agregar espacio inferior entre la imagen y el texto */
        margin-right: 0;
        /* Quitar el margen derecho en pantallas más pequeñas */
    }

    .content-wrapper-moni2 h5 {
        margin-left: 0;
        /* Quitar el margen izquierdo en pantallas más pequeñas */
    }
}