/* Media query para pantallas más pequeñas */
@media (max-width: 991.98px) {
    .navbar-collapse {
        max-height: 400px;
        /* Altura máxima del menú desplegable */
        overflow-y: auto;
        /* Scroll vertical si es necesario */
    }

    .navbar-toggler {
        margin-right: 10px;
        /* Espaciado derecho del botón de hamburguesa */
    }

    .navbar-nav {
        margin-top: 10px;
        /* Espaciado superior de los enlaces */
        justify-content: center;
        /* Centrado de los enlaces */
        flex-direction: column;
        /* Orientación vertical de los enlaces */
    }

    .nav-link {
        padding: 10px 0;
        /* Padding interno de los enlaces */
        text-align: center;
        /* Centrado del texto */
    }

    .navbar-brand {
        margin-bottom: 10px;
        /* Espaciado inferior del logo */
    }
}