.carruproductcontainer {
    margin-right: 0%;
}

.cardproductosproduct {
    border: 1px solid #939393;
    border-radius: 5px;
    padding: 20px;
    width: 100%;
    box-sizing: border-box;
    margin-right: 10px;
    margin-bottom: 10px;
}

.cardproductosproduct h3 {
    margin-bottom: 10px;
}

.cardproductosproduct img {
    width: 100%;
    height: 250px;
    object-fit: cover;
    border-radius: 5px;
}

.cardscontenedorcarru .slick-slide {
    padding-right: 50px;
    padding-left: 50px;
}

.cardscontenedorcarru .slick-slide:first-child {
    padding-right: 0;
}