.bloghomecontainer {
    margin-right: 5%;
}

.cardbloghome {
    border: 1px solid #939393;
    border-radius: 5px;
    padding: 20px;
    width: 100%;
    box-sizing: border-box;
    margin-right: 10px;
    margin-bottom: 10px;
}

.cardbloghome h6 {
    margin-bottom: 10px;
    color: black;
}

.cardbloghome img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 5px;
}

.cardscontenedorbloghome .slick-slide {
    padding-right: 50px;
    padding-left: 50px;
}

.cardscontenedorbloghome .slick-slide:first-child {
    padding-right: 0;
}