.container-inst {
    position: relative;
    overflow: hidden;
}

.content-inst {
    position: relative;
    width: 100%;
}

.image-container-inst {
    position: relative;
    width: 100%;
    height: 500px;
    /* Altura deseada para la imagen */
}

.image-container-inst img {
    width: 100%;
    height: 220%;
    object-fit: cover;
}

.image-overlay-inst {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    /* Color oscuro con opacidad del 50% */
}

.centered-text-inst {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #fff;
    width: 80%;
    z-index: 1;
    /* Asegura que el texto esté por encima de la capa oscura */
}

.logo-inst img {
    width: 140px;
    /* Ancho deseado del logo */
    height: auto;
    /* Altura automática para mantener la proporción */
    filter: invert(1);

}


.titulo-servicios-inst {
    font-size: 36px;
    /* Tamaño del título */
    margin-bottom: 16px;
}

h5 {
    font-size: 20px;
    /* Tamaño del subtítulo */
}