.container-est {
    position: relative;
    overflow: hidden;
}

.content-est {
    position: relative;
    width: 100%;
}

.image-container-est {
    position: relative;
    width: 100%;
    height: 500px;
    /* Altura deseada para la imagen */
}

.image-container-est img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.image-overlay-est {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* Color oscuro con opacidad del 50% */
}

.centered-text-est {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #fff;
    width: 80%;
    z-index: 1;
    /* Asegura que el texto esté por encima de la capa oscura */
}

.est-logo img {
    width: 140px;
    /* Ancho deseado del logo */
    height: auto;
    /* Altura automática para mantener la proporción */
    filter: invert(1);
    /* Invierte los colores de la imagen (cambia negro a blanco) */

}


.titulo-servicios-est {
    font-size: 36px;
    /* Tamaño del título */
    margin-bottom: 16px;
}

h5 {
    font-size: 20px;
    /* Tamaño del subtítulo */
}

.contenedor-entero {
    background-image: linear-gradient(to bottom, #ffffff, #d8deff);
    margin-top: 5%;
    padding-bottom: 5%;
}

.est-contenido-text h5 {
    flex: 1;
    font-size: 20px;
    line-height: 1.4;
    /* Ajustar el line-height para alinear mejor las líneas */
    text-align: justify;
    /* Justificar el texto para alinear las líneas al final */
    margin-left: 10%;
    margin-right: 10%;

    /* Ajustar el margen izquierdo según sea necesario */
    ;
}

.est-imagenes-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 0;
    /* Asegura que no haya margen inferior */

}

.est-imagen {
    width: 45%;
    margin-bottom: 0;
    /* Asegura que no haya margen inferior */
    padding: 10px;
    /* Añade un espacio interno para separar las imágenes */
    box-sizing: border-box;
    /* Incluye el padding en el tamaño total del elemento */
    height: auto;
    /* Ajusta automáticamente la altura al contenido interno */
    display: flex;
    /* Utiliza flexbox para centrar la imagen verticalmente */
    justify-content: center;
    /* Centra horizontalmente la imagen */
}

.est-imagen img {

    margin-top: 10%;
    max-width: 100%;
    /* Ajusta el ancho máximo de la imagen */
    max-height: 400px;
    /* Establece la altura máxima deseada para la imagen */
    object-fit: cover;
    /* Ajusta la imagen para cubrir el contenedor */
    transition: transform 0.3s ease;
    border-radius: 10px;


}

.est-imagen3 {
    width: 45%;
    margin-bottom: 0;
    /* Asegura que no haya margen inferior */
    padding: 10px;
    /* Añade un espacio interno para separar las imágenes */
    box-sizing: border-box;
    /* Incluye el padding en el tamaño total del elemento */
    height: auto;
    /* Ajusta automáticamente la altura al contenido interno */
    display: flex;
    /* Utiliza flexbox para centrar la imagen verticalmente */
    justify-content: center;
    /* Centra horizontalmente la imagen */
}

.est-imagen3 img {

    margin-top: 10%;
    width: 82%;
    /* Ajusta el ancho máximo de la imagen */
    max-height: 400px;
    /* Establece la altura máxima deseada para la imagen */
    transition: transform 0.3s ease;
    border-radius: 10px;

}


.est-imagen1 {
    width: 45%;
    margin-bottom: 0;
    /* Asegura que no haya margen inferior */

    /* Ajusta el ancho de cada imagen */
}



.est-imagen1 img {
    margin-top: 10%;
    margin-left: 10%;
    margin-bottom: 0;
    /* Asegura que no haya margen inferior */
    width: 80%;
    height: 90%;
    /* Ajusta la imagen para que ocupe completamente su contenedor */
    transition: transform 0.3s ease;
    border-radius: 10px;


}

.est-imagen1:hover img {
    transform: scale(1.1);
}

.est-imagen3:hover img {
    transform: scale(1.1);
}

.est-imagen:hover img {
    transform: scale(1.1);
}