.cont-entero-dis {

    padding: 20px;
    background-image: linear-gradient(to bottom, #ffffff, #d8deff);
}

.container-dis {
    position: relative;
    overflow: hidden;
}

.content-dis {
    position: relative;
    width: 100%;
}

.image-container-dis {
    position: relative;
    width: 100%;
    height: 500px;
    /* Altura deseada para la imagen */
}

.image-container-dis img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.image-overlay-dis {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}

.centered-text-dis {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #fff;
    width: 80%;
    z-index: 1;
    /* Asegura que el texto esté por encima de la capa oscura */
}

.dis-logo img {
    width: 140px;
    /* Ancho deseado del logo */
    height: auto;
    /* Altura automática para mantener la proporción */
    filter: invert(1);
    /* Invierte los colores de la imagen (cambia negro a blanco) */

}


.titulo-servicios-dis {
    font-size: 36px;
    /* Tamaño del título */
    margin-bottom: 16px;
}

h5 {
    font-size: 20px;
    /* Tamaño del subtítulo */
}

.Diseno-Principal {
    background-color: aliceblue;
}

.container-diseno {
    margin-left: 120px;
    /* Margen a la izquierda */
    margin-right: 120px;
    /* Margen a la derecha */
}

/* Estilos adicionales para el contenido dentro del contenedor */
.content-diseno {
    margin-top: 20px;
    /* Margen superior */
    /* Puedes agregar más estilos según sea necesario */
}

.card-body-diseno {
    flex: 1;
    font-size: 20px;
    line-height: 1.4;
    /* Ajustar el line-height para alinear mejor las líneas */
    text-align: justify;
    /* Justificar el texto para alinear las líneas al final */
    margin-left: 20px;
    /* Ajustar el margen izquierdo según sea necesario */
    ;
}

.dis-imagen {
    flex: 1;
    position: relative;
    overflow: hidden;
    width: 110px;
    padding-left: 30px;
}

.dis-imagen img {
    object-fit: cover;
    transition: transform 0.3s ease;
    border-radius: 10px;
}

.dis-imagen:hover img {
    transform: scale(1.1);
}