.news-detail-page {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.news-detail-container {
  display: flex;
  width: 100%;
}

.news-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 60px;
  /* Agrega margen de 20px a ambos lados del contenido */
  margin-bottom: 40px;
  /* Agrega margen inferior para separar del contenedor de noticias anteriores */
}

.main-news-image {
  width: 80%;
  /* Ajustar el tamaño de la imagen principal */
  max-height: 500px;
  /* Altura máxima para evitar que la imagen sea demasiado grande */
}

.previous-news-container {
  width: 17%;
  margin-left: 20px;
  margin-top: 4%;

  /* Ajustar el margen izquierdo para separar las secciones */
}

.previous-news-container h3 {
  margin-right: 40px;

}

.previous-news-card {
  display: flex;
  flex-direction: column;
  /* Cambiar la dirección de los elementos a columna */
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 20px;
  margin-right: 50px;
  margin-left: 0px;
  padding: 10px;
}

.previous-news-image {
  width: 182px;
  /* Tamaño fijo para las imágenes de noticias anteriores */
  height: 150px;
  /* Espaciado entre la imagen y el contenido */
}

.previous-news-card h4 {
  margin-top: 0;
  text-align: center;
  /* Centrar el texto */
}

.previous-news-container h3 {
  margin-top: 0;
  text-align: center;
  /* Centrar el texto */
}

.previous-news-card:first-child {
  margin-top: 20px;
  /* Agrega margen superior al primer elemento para separarlo del encabezado */
}