.card-title {
  position: absolute;
  top: 92%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: rgb(0, 0, 0);
  padding: 10px;
  border-radius: 5px;
  font-size: 1.2em;
  font-weight: bold;
  text-align: center;
  max-width: 90%;
  z-index: 1;
  /* Aseguramos que el título esté encima de la imagen */
}

.card-group {
  overflow-x: hidden;
}

.card-img-top {
  border-radius: 10px !important;
  width: calc(100% - 20px);
  height: 200px;
  object-fit: cover;
  margin: 10px;
  position: relative;
  filter: brightness(0.9) saturate(100%) contrast(100%);
  transition: filter 0.3s;
  /* Agregamos transición para suavizar el cambio */
}

.card {
  border: none !important;
  /* Elimina los bordes de las tarjetas */
  position: relative;
  overflow: hidden;
  background-color: transparent;
}


.card:hover .overlay {
  opacity: 1;
}

.card:hover .card-img-top {
  filter: brightness(70%);
}

.content {
  text-align: center;
}

.titulo-servicios {
  color: rgb(99, 157, 5);
  font-weight: bold;
  text-align: center;
}

.titulo-servicios2 {
  color: rgb(91, 109, 159);
  font-weight: bold;
  text-align: center;
}

.content p {
  color: rgb(53, 70, 119);
  flex: 1;
  font-size: 20px;
  line-height: 1.4;
  /* Ajustar el line-height para alinear mejor las líneas */
  text-align: justify;
  /* Justificar el texto para alinear las líneas al final */

}

.dropdown-toggle::after {
  border-top: 0.3em solid white !important;
  border-right: 0.3em solid transparent !important;
  border-left: 0.3em solid transparent !important;
}

.card-text {
  color: rgb(57, 49, 49);
  font-weight: bold;
  text-align: center;
}

.imagentele {
  border-radius: 10px !important;
  width: calc(100% - 20px);
  height: 260px;
  margin: 10px;
  transition: filter 0.3s;
}

.navbar-wrapper {
  margin-bottom: 50px;

}

.content-wrapper {
  margin-top: 50px;
}

.navbar-brand {
  transform: translateY(12px);
  color: rgb(255, 255, 255);
  font-weight: bold;
  text-align: center;
  text-shadow: 3px 3px 3px rgb(0, 0, 0);
}

.carousel-item img {
  height: 400px;
  object-fit: cover;
  /* Para que la imagen cubra el espacio del contenedor */
}

.collapse.navbar-collapse {
  transform: translateX(-70px);

}

.whatsapp-float {
  position: fixed;
  width: 60px;
  height: 60px;
  bottom: 40px;
  left: 40px;
  background-color: #25d366;
  color: #fff;
  border-radius: 50px;
  text-align: center;
  font-size: 30px;
  box-shadow: 2px 2px 3px #999;
  z-index: 100;
}

.whatsapp-float:hover {
  background-color: #128c7e;
}

.whatsapp-icon {
  margin-top: 15px;
}

.navbar-opaque {
  background-color: rgba(0, 0, 0, 0.8) !important;
  /* Cambia 0.8 al valor de opacidad deseado (de 0 a 1) */
}

.telegraf {
  transform: translateX(-3px);
}

.pipe {
  transform: translate(-5px, -2px);
}


.scrollToTopButton {
  position: fixed !important;
  /* Hace que el botón se mantenga fijo en la ventana del navegador */
  bottom: 40px !important;
  /* Posiciona el botón 20px desde la parte inferior */
  right: 40px !important;
  /* Posiciona el botón 20px desde el lado derecho */
  background-color: #04a8df !important;
  /* Color de fondo del botón */
  color: white !important;
  /* Color del texto del botón */
  border: none !important;
  /* Elimina el borde del botón */
  border-radius: 50% !important;
  /* Hace que el botón tenga bordes redondeados */
  width: 40px !important;
  /* Ancho del botón */
  height: 40px !important;
  /* Altura del botón */
  display: flex !important;
  /* Hace que los elementos dentro del botón se muestren en línea */
  align-items: center !important;
  /* Centra verticalmente los elementos dentro del botón */
  justify-content: center !important;
  /* Centra horizontalmente los elementos dentro del botón */
  cursor: pointer !important;
  /* Cambia el cursor al pasar sobre el botón */
  transition: background-color 0.3s ease !important;
  /* Agrega una transición suave al cambiar el color de fondo */
}

.scrollToTopButton:hover {
  background-color: #0056b3 !important;
  /* Cambia el color de fondo cuando se pasa el cursor sobre el botón */
}



.homeimage1 {
  transition: transform 0.3s ease;
}

.homeimage1:hover {
  transform: scale(1.1);
}

.moni-home {
  border-radius: 10px !important;
  width: calc(100% - 20px);
  height: 200px;
  margin: 10px;
  position: relative;
  filter: brightness(0.9) saturate(100%) contrast(100%);
  transition: filter 0.3s;
}


.card:hover .moni-home {
  filter: brightness(70%);
}

/* RESPONSIVE SECCION */

@media (max-width: 768px) {
  .card-title {
    font-size: 1em;
    /* Reducir el tamaño del título en pantallas más pequeñas */
  }
}